import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class ConfigureViber extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Configure Viber</h1>
                </div>
                <form>
                    <div className="menu-content">
                            <ul>
                                <li><InputBox title="Attach Viber media Incident" name="viberMediaIncident" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Set Viber Messanger Attachment Type" name="viberAttachmentType" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Set Viber Storage System" name="storageSystem" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Store Viber Messanger Attachment with BlueLeap" name="attachmentBlueleap" onUpdate={this.upDateData}/></li>
                            </ul>
                            <ul>
                                <li><ToggleButton title="Enable Viber Messanger Voice" name="viberVoice" onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable Viber Messanger Text" name="viberText" onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable Viber Messanger Video" name="viberVideo" onUpdate={this.upDateData}/></li>
                            </ul>
                    </div>
                    <div>
                        <input type="button" value="save"/>
                    </div>
                </form>
            </div>
        );
    }
}

export { ConfigureViber };


