import React, { Component } from 'react';
import '../styles/css/configure_sms.css';
import { InputBox, ToggleButton } from '../component';

class ConfigureSMS extends Component {
    constructor() {
        super()
        this.state = {
            formData: {},
        }
        this.upDateData = this.upDateData.bind(this);
    }
    upDateData(e, type) {
        let data = this.state.formData;
        if (type === "checkbox") {
            data[e.target.name] = e.target.checked;
        }
        if (type === "inputbox") {
            data[e.target.name] = e.target.value;
        }
        this.setState({ formData: data })
    }
    render() {
        return (
            <div className="menu-main-content">
                <div className="menu-title">
                    <h1>Configure SMS</h1>
                </div>
                <form>
                    <div className="menu-content">
                        <div className="configure-input">
                            <ul>
                                <li>
                                    <InputBox title="SMS Alpha Numeric" name="smsAlphaNumeric" onUpdate={this.upDateData} />
                                </li>
                                <li>
                                    <InputBox title="Enter SMS Short Code" name="shortCode" onUpdate={this.upDateData} />
                                </li>
                                <li>
                                    <InputBox title="Auto Response Message" name="responseMsg" onUpdate={this.upDateData} />
                                </li>
                            </ul>
                        </div>
                        <div className="configure-switch">
                            <ul>
                                <li>
                                    <ToggleButton title="SMS Create Inbound Incident" name="inboundIncident" onUpdate={this.upDateData} />
                                </li>
                                <li>
                                    <ToggleButton title="SMS Create Outbound Incident" name="outboundIncident" onUpdate={this.upDateData} />
                                </li>
                                <li>
                                    <ToggleButton title="Enable Auto Response" name="autoResponse" onUpdate={this.upDateData} />
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div>
                        <input type="button" value="save" />
                    </div>
                </form>
            </div>
        );
    }
}

export { ConfigureSMS };


