import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class ConfigureLINE extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Configure LINE</h1>
                </div>
                <form>
                    <div className="menu-content">
                            <ul>
                                <li><InputBox title="Attach LINE media Incident" name="incident" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Set LINE Messanger Attachment Type" name="attachment_type" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Set LINE Storage System" name="storage_system" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Store LINE Messanger Attachment with BlueLeap" name="attachment_blueleap" onUpdate={this.upDateData}/></li>
                            </ul>
                            <ul>
                                <li><ToggleButton title="Enable LINE Messanger Voice" name="Linevoice"  onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable LINE Messanger Text" name="Linetext" onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable LINE Messanger Video" name="Linevideo" onUpdate={this.upDateData} /></li>
                            </ul>
                    </div>
                    <div>
                        <input type="button" value="save" onClick={this.upDateData}/>
                    </div>
                </form>
            </div>
        );
    }
}

export { ConfigureLINE };


