import React, { Component } from 'react';
import { RequiredFieldInputBox as Required, InputBox, DropDown, DatePicker } from '../component';

class Billing extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox" || type==="dropdown"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div>
                <div className="menu-main-content">
                    <div className="menu-title">
                        <h1>Billing</h1>
                    </div>
                    <div className="menu-content">
                        <div className="billing">
                            <ul>
                                <li><InputBox title="Credit Card Number" name="creditCardNumber" onUpdate={this.upDateData}/></li>
                                <li><Required title="Street1" name="street1" onUpdate={this.upDateData}/></li>
                                <li><Required title="Street2" name="street2" onUpdate={this.upDateData}/></li>
                            </ul>
                            <ul>
                                <li><DropDown title="State" name="state" onUpdate={this.upDateData}/></li>
                                <li><Required title="Zip" name="zip" onUpdate={this.upDateData}/></li>
                                <li><Required title="Country" name="country" onUpdate={this.upDateData}/></li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="menu-main-content second">
                    <div className="menu-title">
                        <h1>Monthly Invoice</h1>
                    </div>
                    <div className="menu-content">
                       
                        <div className="billing">
                            <ul>
                                <li>
                                <DatePicker title="Invoice Dates" name="invoiceDate"  onUpdate={this.upDateData}/>
                                </li>
                            </ul>
                            <ul>
                                <li><InputBox title="Item Type" name="itemType" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Price Per Item" name="pricePerItem" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="No.Of Items" name="noOfItems" onUpdate={this.upDateData}/></li>
                            </ul>
                            <ul>
                                <li><InputBox title="Total" name="total" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Grand Total" name="grandTotal" onUpdate={this.upDateData}/></li>
                            </ul>
                        </div>
                    </div>
                </div> 
                    <div>
                        <input type="button" value="save" onClick={this.upDateData}/>
                    </div>
          </div>
        );
    }
}

export { Billing };


