import React, { Component } from 'react';
import '../styles/css/input_box.css';

class InputBoxUploadButton extends Component{
    constructor(props){
        super(props)
        this.state={
            filepath:''
        }
        this.getFile=this.getFile.bind(this);
    }
    getFile(e){
        this.props.onUpdate(e,"file")
        this.setState({filepath:e.target.file})
    }
    render(){
        return(
            <div className="input-box-upload">
                <div className="itemName">{this.props.title ? this.props.title:''}</div>
                <div className="choose_file">
                    <input type="file" name={this.props.name} onChange={this.getFile}/>
                    <label className="file" >
                        <div className="button">
                            <img src='/images/ic_upload.png' />
                            <span>Upload</span>
                        </div>
                    </label>
                </div>
         </div>
        );
    }
}

export { InputBoxUploadButton };


