import {userService} from "../_services";

export async function appAuthHeader() {
  let app = JSON.parse(localStorage.getItem('app'));

  if (!app || !app.token) {
     app = await userService.generateAppToken();
  }

  return (app && app.token) ? { 'Authorization': app.token } : {};
}


