import React, { Component } from 'react';
import {alertActions} from "../_actions";
import PropTypes from 'prop-types';

class Alert extends Component {
  componentDidMount() {
    const { autoDismiss, millisecondsToDismiss, dispatch } = this.props;

    if(autoDismiss) {
      setTimeout(
        function() {
          dispatch(alertActions.clear());
        },
        millisecondsToDismiss
      );
    }
  }

  render() {
    const { type, children } = this.props;

    return (
      <div className="alert-content">
        <div className={`alert ${type} alert-dismissible fade in`}>
          <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
          {children}
        </div>
      </div>
    );
  }
}

Alert.propTypes = {
  autoDismiss: PropTypes.bool.isRequired,
  millisecondsToDismiss: PropTypes.number.isRequired,
  dispatch: PropTypes.func.isRequired,
  type: PropTypes.string.isRequired,
  children: PropTypes.string.isRequired
};

export { Alert };


