import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import '../styles/css/header.css';

const apiUrl = process.env.NODE_ENV === 'production' ? '/admin-portal' : '';

class Header extends Component {
    constructor() {
        super()

        this.state = {
            isMenuOpen: false
        }

        this.onMenuOpen = this.onMenuOpen.bind(this);
    }

    onMenuOpen() {
        this.setState({ isMenuOpen: !this.state.isMenuOpen })
    }

    render() {
      const { loggedIn } = this.props;

      return (
        <div className="header-container">
          <div className="logo">
            <img src="images/logo.png" alt="" />
          </div>
          {this.state.isMenuOpen ?
            <div className="nav-bar clearfix is-active">
              <div id="nav-toggle" className="active" onClick={this.onMenuOpen}>
                <span></span>
              </div>
              <ul>
                {
                  loggedIn && <li onClick={this.onMenuOpen}><Link to={apiUrl + "/account-info"}><p>Account Information</p></Link></li>
                }
                {
                  loggedIn && <li onClick={this.onMenuOpen}><Link to={apiUrl + "/billing"}><p>Billing</p></Link></li>
                }
                <li onClick={this.onMenuOpen}><p>Help</p></li>
                {
                  loggedIn && <li onClick={this.onMenuOpen}><Link to={apiUrl + "/login"}><p>Log out</p></Link></li>
                }
              </ul>
            </div>
            :
            <div className="nav-bar clearfix">
              <div id="nav-toggle" onClick={this.onMenuOpen}>
                <span></span>
              </div>
              <ul>
                {
                  loggedIn && <Link to={apiUrl + "/account-info"}><li><p>Account Information</p></li></Link>
                }
                {
                  loggedIn && <Link to={apiUrl + "/billing"}><li><p>Billing</p></li></Link>
                }
                <li><p>Help</p></li>
                {
                  loggedIn && <Link to={apiUrl + "/login"}><li><p>Log out</p></li></Link>
                }
              </ul>
            </div>
          }
          </div>
      );
    }
}

function mapStateToProps(state) {
  const { loggedIn } = state.authentication;

  return {
    loggedIn
  };
}

const connectedHeade = connect(mapStateToProps)(Header);
export { connectedHeade as Header };



