import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class ConfigureKik extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Configure KiK</h1>
                </div>
                <form>
                    <div className="menu-content">
                            <ul>
                                <li><InputBox title="Set KiK Attachment Type" name="kikAttachmentType"onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Store KiK Attachment with BlueLeap" name="attachmentBlueLeap"onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Set Kik Storage System" name="storageSystem"onUpdate={this.upDateData}/></li>
                            </ul>
                            <ul>
                                <li><ToggleButton title="Enable KiK text"  name="kikText"onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable KiK voice" name="kikVoice"onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable KiK images" name="kikImage"onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable KiK video" name="kikVideo"onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable KiK stickers" name="kikStickers"onUpdate={this.upDateData}/></li>
                            </ul>
                    </div>
                    <div>
                        <input type="button" value="save"/>
                    </div>
                </form>
            </div>
        );
    }
}

export { ConfigureKik };


