import React, { Component } from 'react';
import '../styles/css/drop_down.css';

class DropDown extends Component {
    render() {
        return (
            <div>
                <div className="itemName">{this.props.title ? this.props.title : ''}{this.props.title ? <span>*</span> : null}</div>
                <div className="drop-down">
                    <div className="select">
                        <select name={this.props.name} onChange={(e) => this.props.onUpdate(e, "dropdown")}>
                            {this.props.value.map((item) => <option key={item} value={item} > {item} </option>)}
                        </select>
                    </div>
                </div>
            </div>
        );
    }
}

export { DropDown };


