import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class ConfigureWeChat extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Configure WeChat</h1>
                </div>
                <form>
                    <div className="menu-content">
                            <ul>
                                <li><InputBox title="Attach to Incident" name="wcAttachIncident" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="See WeChat Attachment Type" name="wcAttachmentType" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="Store WeChat Attachment BlueLeap" name="attachmentBlueleap" onUpdate={this.upDateData}/></li>
                                <li><InputBox title="See WeChat Storage System" name="storageSystem" onUpdate={this.upDateData} /></li>
                            </ul>
                            <ul>
                                <li><ToggleButton title="Enable Text" name="text" onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable Voice" name="voice" onUpdate={this.upDateData}/></li>
                                <li><ToggleButton title="Enable Video" name="video" onUpdate={this.upDateData}/></li>
                            </ul>
                    </div>
                    <div>
                        <input type="button" value="save"/>
                    </div>
                </form>
                
            </div>
        );
    }
}

export { ConfigureWeChat };


