import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class TechnicalAdminSetting extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Technical Admin Setting</h1>
                </div>
                <form>
                <div className="menu-content">
                    <div className="company-info">
                        <ul>
                            <li>
                            <InputBox title="Company Account Name" name="accountName" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="Company Password" name="password" onUpdate={this.upDateData}/>
                            </li>
                        </ul>
                    </div>
                    <div className="wechat-info">
                        <div className="techincal-title-info">
                            <h1>WeChat Account Information</h1>
                        </div>
                        <ul>
                            <li>
                            <InputBox title="WeChat Account ID" name="weChatId" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="WeChat URL"  name="weChatURL" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="WeChat Account User" name="weChatAccountUser" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="WeChat Account Password" name="weChatPassword" onUpdate={this.upDateData}/>
                            </li>
                        </ul>
                    </div>
                    <div className="kik-info">
                        <div className="techincal-title-info">
                            <h1>KiK Account Information</h1>
                        </div>
                        <ul>
                            <li>
                            <InputBox title="KiK Username" name="kikUsername" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="KiK API Key" name="kikApiKey" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="KiK Webbook" name="kikWebbook" onUpdate={this.upDateData}/>
                            </li>
                        </ul>
                    </div>
                    <div className="ftp-info">
                        <div className="techincal-title-info">
                            <h1>FTP Account Information</h1>
                        </div>
                        <ul>
                            <li>
                            <ToggleButton title="Enable FTPS" name="enableFTPS" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="FTP Host Name" name="ftpHost" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="FTP Storage Path" name="ftpStoragePath" onUpdate={this.upDateData}/>
                            </li>
                        </ul>
                    </div>
                    <div className="bluleap-info">
                        <div className="techincal-title-info">
                            <h1>BlueLeap Account Information</h1>
                        </div>
                        <ul>
                            <li>
                            <InputBox title="Credentials for BlueLeap to Store attachments" name="blueleapthStoreAttachment" onUpdate={this.upDateData}/>
                            </li>
                            <li>
                            <InputBox title="Credentials for BlueLeap to Store attachments" name="blueleapthStoreAttachment2" onUpdate={this.upDateData} />
                            </li>
                        </ul>
                    </div>
                </div>
                <div>
                        <input type="button" value="save"/>
                    </div>
                </form>
            </div>
        );
    }
}

export { TechnicalAdminSetting };


