import React, {Component} from 'react';
import {ToggleButton} from '../component';
import '../styles/css/global_setting.css';
import {userActions} from "../_actions";
import connect from "react-redux/es/connect/connect";

class GlobalSetting extends Component {
  _isMounted = false;

  constructor() {
    super()

    this.state = {
      settings: {},
    }

    this.upDateData = this.upDateData.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this._isMounted = true;

    const { dispatch, user } = this.props;

    dispatch(userActions.settings(user.username));
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  upDateData(e, type) {
    let { settings } = this.props;

    if (type === "checkbox") {
      settings[e.target.name] = e.target.checked;
    }
    if (type === "inputbox") {
      settings[e.target.name] = e.target.value;
    }

    this.setState({ settings })
  }

  handleSubmit(e) {
    e.preventDefault();

    const { dispatch, settings, user } = this.props;

    dispatch(userActions.update(settings, user.username));
  }

  render() {
    const { settingsFetched, settings } = this.props;

    return (
      <div className="menu-main-content">
        <div className="menu-title">
          <h1>Global Settings</h1>
        </div>
        {
          settingsFetched && settings &&
          <form className="global-setting">
            <div className="menu-content">
              <ul>
                <li className="items">
                  <ToggleButton title="Enable SMS" name="GlobalEnableSMS" checked={settings.GlobalEnableSMS} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable MMS" name="GlobalEnableMMS" checked={settings.GlobalEnableMMS} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable WeChat" name="GlobalEnableWeChat" checked={settings.GlobalEnableWeChat} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable LINE" name="GlobalEnableLINE" checked={settings.GlobalEnableLINE} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable Facebook Messanger" name="GlobalEnableFBMessenger" checked={settings.GlobalEnableFBMessenger} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable Viber" name="GlobalEnableViber" checked={settings.GlobalEnableViber} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable KiK" name="GlobalEnableKiK" checked={settings.GlobalEnableKiK} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable Voice" name="GlobalEnableVoice" checked={settings.GlobalEnableVoice} onUpdate={this.upDateData}/>
                </li>
                <li className="items">
                  <ToggleButton title="Enable WebRTC" name="GlobalEnableWebRTC" checked={settings.GlobalEnableWebRTC} onUpdate={this.upDateData}/>
                </li>
              </ul>
            </div>
            <div>
              <input type="button" value="save" onClick={this.handleSubmit}/>
            </div>
          </form>
        }
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { settingsFetched, settings } = state.settings;
  const { user } = state.authentication;

  return {
    settingsFetched,
    settings,
    user
  };
}

const connectedGlobalSetting = connect(mapStateToProps)(GlobalSetting);
export { connectedGlobalSetting as GlobalSetting };


