import React, { Component } from 'react';
import { InputBox, ToggleButton, DropDown, InputBoxUploadButton } from '../component';
import '../styles/css/configure_video.css';

class ConfigureVideo extends Component {
    constructor() {
        super()
        this.state = {
            formData: {},
        }
        this.upDateData = this.upDateData.bind(this);
    }
    upDateData(e, type) {
        let data = this.state.formData;
        if (type === "checkbox") {
            data[e.target.name] = e.target.checked;
        }
        if (type === "inputbox") {
            data[e.target.name] = e.target.value;
        }
        if (type === "file") {
            data[e.target.name] = e.target.files;
        }
        this.setState({ formData: data })
    }
    render() {
        return (
            <div className="menu-main-content">
                <div className="menu-title">
                    <h1>Configure Video</h1>
                </div>
                <form>
                    <div className="menu-content">
                        <div className="configure-video">
                            <div className="video-content">
                                <ul>
                                    <li>
                                        <ToggleButton title="Record Session" name="recordSession" onUpdate={this.upDateData} />
                                    </li>
                                    <li>
                                        <InputBox title="Set Max Duration (in minutes)" name="setMaxDuration" onUpdate={this.upDateData} />
                                    </li>
                                    <li>
                                        <InputBox title="Set Max Number of Participants" name="setMaxNumberParticipants" onUpdate={this.upDateData} />
                                    </li>
                                </ul>
                            </div>
                            <div className="video-content">
                                <ul>
                                    <li>
                                        <DropDown title="Set Streaming" name="setStreaming" value={['Bidirectional', 'One Way From Agent', 'One Way from Customer']} onUpdate={this.upDateData} />
                                    </li>
                                    <li>
                                        <InputBoxUploadButton title="Attach to incident, Link to Incident" name="recordingAttachment" onUpdate={this.upDateData} />
                                    </li>
                                    <li>
                                        <DropDown title="Recording Storage" name="recordingStorage" value={['Within CRM', 'Within Dialogue', 'Within Local Server']} onUpdate={this.upDateData} />
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div>
                        <input type="button" value="save" />
                    </div>
                </form>
            </div>
        );
    }
}

export { ConfigureVideo };


