export function backendUrl() {
  switch(process.env.NODE_ENV) {
    case 'production':
      return 'https://dialogue.blueleap.com/api/v1/'
    case 'dev':
      return 'https://dialogue-dev.blueleap.com/api/v1/'
    case 'staging':
      return 'https://dialogue-staging.blueleap.com/api/v1/'
    default:
      return 'http://127.0.0.1:5000/api/v1/'
  }
}


