import { userConstants } from '../_constants';
import { userService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';

export const userActions = {
  login,
  logout,
  register,
  settings,
  update
};

function login(username, password) {
  return dispatch => {
    dispatch(request({ username }));

    userService.login(username, password)
      .then(
        user => {
          dispatch(success(user));
          history.push('/');
          dispatch(alertActions.success('Login successful'));
        },
        error => {
          dispatch(failure(error.toString()));
          dispatch(alertActions.error('Wrong username or password'));
        }
      );
  };

  function request(user) { return { type: userConstants.LOGIN_REQUEST, user } }
  function success(user) { return { type: userConstants.LOGIN_SUCCESS, user } }
  function failure(error) { return { type: userConstants.LOGIN_FAILURE, error } }
}

function logout() {
  userService.logout();
  return { type: userConstants.LOGOUT };
}

function register(user) {
  return dispatch => {
    dispatch(request(user));

    userService.register(user)
      .then(
        user => {
          dispatch(success());
          history.push('/login');
          dispatch(alertActions.success('Registration successful'));
        },
        error => {
          dispatch(failure(error.toString()));
          dispatch(alertActions.error(error.toString()));
        }
      );
  };

  function request(user) { return { type: userConstants.REGISTER_REQUEST, user } }
  function success(user) { return { type: userConstants.REGISTER_SUCCESS, user } }
  function failure(error) { return { type: userConstants.REGISTER_FAILURE, error } }
}

function settings(username) {
  return dispatch => {
    dispatch(request());

    userService.getSettings(username)
      .then(
        settings => {
          dispatch(success(settings));
        },
        error => {
          dispatch(failure(error.toString()));
          dispatch(alertActions.error('Something went wrong while retrieving user data'));
        }
      );
  };

  function request() { return { type: userConstants.TOKEN_INFO_REQUEST } }
  function success(settings) { return { type: userConstants.TOKEN_INFO_SUCCESS, settings } }
  function failure(error) { return { type: userConstants.TOKEN_INFO_FAILURE, error } }
}

function update(settings, username) {
  return dispatch => {
    dispatch(request(settings));

    userService.update(settings, username)
      .then(
        body => {
          dispatch(success(body));
          dispatch(alertActions.success('User updated successfully'));
        },
        error => {
          dispatch(failure(error.toString()));
          dispatch(alertActions.error('Something went wrong while updating user data'));
        }
      );
  };

  function request(body) { return { type: userConstants.UPDATE_REQUEST, body } }
  function success(body) { return { type: userConstants.UPDATE_SUCCESS, body } }
  function failure(error) { return { type: userConstants.UPDATE_FAILURE, error } }
}


