import React, { Component } from 'react';
import '../styles/css/input_box.css';

class RequiredFieldInputBox extends Component{
    render(){
        return(
            <div className="input-box">
               <div className="itemName">{this.props.title ? this.props.title:''}{this.props.title ?<span>*</span>: null}</div>
                <input type="text" name={this.props.name} onChange={(e)=>this.props.onUpdate(e,"inputbox")}/>
            </div>
        );
    }
}

export { RequiredFieldInputBox };


