import { userConstants } from '../_constants';

let bl_account_settings = JSON.parse(localStorage.getItem('bl_account_settings'));

const initialState = bl_account_settings ? { settingsFetched: true, bl_account_settings } : {};

export function settings(state = initialState, action) {
  switch (action.type) {
    case userConstants.TOKEN_INFO_REQUEST:
      return {
        fetchingSettings: true,
      };
    case userConstants.TOKEN_INFO_SUCCESS:
      return {
        settingsFetched: true,
        settings: action.settings
      };
    case userConstants.TOKEN_INFO_FAILURE:
      return {};
    default:
      return state
  }
}


