import { Route } from 'react-router-dom';
import React, { Component } from 'react';
import { PrivateRoute } from './_components';
import {
  HomePage,
  LoginPage,
  RegisterPage,
  GlobalSetting,
  ConfigureSMS,
  ConfigureVideo,
  ConfigureWeChat,
  ConfigureLINE,
  ConfigureFbSetting,
  ConfigureViber,
  ConfigureKik,
  TechnicalAdminSetting,
  AccountInformation,
  Billing
} from './pages';

const apiUrl = '';

class routes extends Component {
    render() {
        return (
            <div>
                <PrivateRoute exact path={apiUrl + "/"} component={HomePage} />
                <Route path={apiUrl + "/globalsetting"} component={GlobalSetting} />
                <Route path={apiUrl + "/configuresms"} component={ConfigureSMS} />
                <Route path={apiUrl + "/configurevideo"} component={ConfigureVideo} />
                <Route path={apiUrl + "/configurewechat"} component={ConfigureWeChat} />
                <Route path={apiUrl + "/configureline"} component={ConfigureLINE} />
                <Route path={apiUrl + "/configurefb"} component={ConfigureFbSetting} />
                <Route path={apiUrl + "/configureviber"} component={ConfigureViber} />
                <Route path={apiUrl + "/configurekik"} component={ConfigureKik} />
                <Route path={apiUrl + "/technical-admin-setting"} component={TechnicalAdminSetting} />
                <Route path={apiUrl + "/account-info"} component={AccountInformation} />
                <Route path={apiUrl + "/billing"} component={Billing} />
                <Route path={apiUrl + "/login"} component={LoginPage} />
                <Route path={apiUrl + "/register"} component={RegisterPage} />
            </div>
        );
    }
}

export default routes;


