import axios from 'axios';
import {appAuthHeader, userAuthHeader, backendUrl} from "../_helpers";

export const userService = {
  login,
  logout,
  register,
  generateAppToken,
  getSettings,
  update
};

const appUsername = 'blueleaptest01';
const appPassword = 'blueleapisawesome123';

function login(username, password) {
  const data = {
    username: username,
    password: password
  };

  return axios.post(backendUrl() + 'loginusers/login/', data)
    .then((response) => {
      const user = {
        username: response.data.username,
        email: response.data.email,
        token: response.data.token,
        expires_in: response.data.expires_in,
        is_admin: response.data.is_admin,
        bl_user_accounts: response.data.bl_user_accounts,
        timeout: new Date().getTime() + response.data.expires_in*1000
      };

      localStorage.setItem('user', JSON.stringify(user));

      return user;
    });
}

function logout() {
  localStorage.removeItem('user');
  localStorage.removeItem('bl_account_settings');
  localStorage.removeItem('app');
}

async function register(user) {
  const config = {
    headers: await appAuthHeader()
  };

  const data = {
    username: user.username,
    email: user.email,
    password: user.password
  };

  return axios.post(backendUrl() + 'loginusers/', data, config)
    .then((response) => {
      return response.data;
    });
}

async function generateAppToken() {
  const config = {
    headers: {'Authorization': 'Basic ' + decodeURIComponent(escape(btoa(appUsername + ':' + appPassword)))}
  };

  try {
    const appPromise = axios.get(backendUrl() + 'token/', config);
    const response = await appPromise;
    const app = {
      token: response.data.token
    };

    localStorage.setItem('app', JSON.stringify(app));

    return app;
  } catch (e) {
    console.error(e);
  }
}

function getSettings(username) {
  const config = {
    headers: userAuthHeader()
  };

  return axios.get(backendUrl() + 'loginusers/bluseraccounts/' + username + '/', config)
    .then((response) => {
      localStorage.setItem('bl_account_settings', JSON.stringify(response.data));

      return response.data;
    });
}

 function update(body, username) {
  const config = {
    headers:  userAuthHeader()
  };

  return axios.put(backendUrl() + 'loginusers/bluseraccounts/' + username + '/', body, config)
    .then((response) => {
      return response.data;
    });
}


