import React, { Component } from 'react';
import { InputBox, ToggleButton } from '../component';

class ConfigureFbSetting extends Component{
    constructor(){
        super()
        this.state={
            formData:{},
        }
        this.upDateData=this.upDateData.bind(this);
    }
    upDateData(e,type){
        let data = this.state.formData;
        if(type==="checkbox"){
            data[e.target.name]=e.target.checked;
        }
        if(type==="inputbox"){
            data[e.target.name]=e.target.value;
        }
        this.setState({formData:data})
    }
    render(){
        return(
            <div className="menu-main-content">
                <div className="menu-title">
                <h1>Configure Facebook Messanger</h1>
                </div>
                <form>
                <div className="menu-content">
                    <ul>
                        <li><InputBox title="Attach Facebook media Incident" name="fbMediaIncident" onUpdate={this.upDateData}/></li>
                        <li><InputBox title="Set Facebook Messanger Attachment Type" name="fbAttachmentType" onUpdate={this.upDateData}/></li>
                        <li><InputBox title="Set Facebook Storage System" name="storageSyatem" onUpdate={this.upDateData}/></li>
                        <li><InputBox title="Store Facebook Messanger Attachment with BlueLeap" name="attachmentBlueLeap" onUpdate={this.upDateData}/></li>
                    </ul>
                    <ul>
                        <li><ToggleButton title="Enable Facebook Messanger Voice" name="fbVoice" onUpdate={this.upDateData}/></li>
                        <li><ToggleButton title="Enable Facebook Messanger Text" name="fbText" onUpdate={this.upDateData}/></li>
                        <li><ToggleButton title="Enable Facebook Messanger Video" name ="fbVideo" onUpdate={this.upDateData}/></li>
                    </ul>
                </div>
                <div>
                    <input type="button" value="save"/>
                </div>
                </form>
            </div>
        );
    }
}

export { ConfigureFbSetting };


