import React, { Component } from 'react';
import { RequiredFieldInputBox as Required, DropDown } from '../component';

class AccountInformation extends Component {
    constructor() {
        super()
        this.state = {
            formData: {},
        }
        this.upDateData = this.upDateData.bind(this);
    }
    upDateData(e, type) {
        let data = this.state.formData;
        if (type === "checkbox") {
            data[e.target.name] = e.target.checked;
        }
        if (type === "inputbox") {
            data[e.target.name] = e.target.value;
        }
        this.setState({ formData: data })
    }
    render() {
        return (
            <div className="menu-main-content">
                <div className="menu-title">
                    <h1>Account Information</h1>
                </div>
                <form>
                    <div className="menu-content">
                        <div className="account-info">
                            <ul>
                                <li><Required title="Company Name" name="companyName" onUpdate={this.upDateData} /></li>
                                <li><Required title="Street1" name="street1" onUpdate={this.upDateData} /></li>
                                <li><Required title="Stree2" name="street2" onUpdate={this.upDateData} /></li>
                            </ul>
                            <ul>
                                <li><DropDown title="State" name="state" value={['Maharastra', 'Gujarat']} onUpdate={this.upDateData} /></li>
                                <li><Required title="Zip" name="zip" onUpdate={this.upDateData} /></li>
                                <li><Required title="Country" name="country" onUpdate={this.upDateData} /></li>
                            </ul>
                            <ul>
                                <li><Required title="Accounts Payable Contact Name" name="accountPayablename" onUpdate={this.upDateData} /></li>
                                <li><Required title="Accounts Payable Contact Email" name="accountPayableEmail" onUpdate={this.upDateData} /></li>
                                <li><Required title="Accounts Payable Contact Phone" name="accountPayableContact" onUpdate={this.upDateData} /></li>
                            </ul>
                            <ul>
                                <li><Required title="Technical Contact Name" name="technicalContactNumber" onUpdate={this.upDateData} /></li>
                                <li><Required title="Technical Contact Email" name="technicalContactEmail" onUpdate={this.upDateData} /></li>
                                <li><Required title="Technical Contact Phone" name="technicalContact Phone" onUpdate={this.upDateData} /></li>
                            </ul>
                        </div>

                    </div>
                    <div>
                        <input type="button" value="save" />
                    </div>
                </form>
            </div>
        );
    }
}

export { AccountInformation };


